.cb-contact-page-conect-open-to-communication {
	/* margin-top: -100px; */
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	margin: 0 auto;
	width: 100%;
	padding: 0px 15px;
	max-width: 1050px;
	background-color: #fff;
}

.cb-contact-page-conect-open-to-communication h2 {
	text-align: left;
	margin: 20px 0 10px;

	color: var(--heading-color);
	line-height: 1.5;
	font-size: 24px;
	font-weight: 700;
	font-family: "Poppins", sans-serif !important;
}
.cb-contact-page-conect-open-to-communication p {
	font-family: "Poppins", sans-serif !important;
	font-size: 16px;
	text-align: left;
	line-height: 1.2;
	color: var(--dark-color);

	margin: 0 0 20px;
}

@media (min-width: 1024px) {
	.cb-contact-page-conect-open-to-communication {
		/* padding: 30px 30px 0; */
	}
	.cb-contact-page-conect-open-to-communication h2 {
		font-size: 27px;
		/* margin: 100px 0 30px; */
	}
	.cb-contact-page-conect-open-to-communication p {
		font-size: 18px;
	}
}
